nav {
  display: -webkit-flex;
  display: flex;
  padding: 1% 3%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

nav img {
  width: 300px;
}

navbar {
  -webkit-flex: 1 1;
          flex: 1 1;
  text-align: right;
}

.navbar ul li {
  list-style: none;
  display: inline-block;
  padding: 8px 12px;
  position: relative;
}

.navbar ul li a {
  color: rgb(13, 17, 58);
  text-decoration: none;
  font-size: 25px;
  font-weight: bold;
  font: bolder;
}

.navbar ul li::after {
  content: "";
  width: 0%;
  height: 2px;
  background: #e4d7d7;
  display: block;
  margin: auto;
  transition: 0.5s;
}

.navbar ul li:hover::after {
  width: 100%;
}

.dropbtn {
  background-color: transparent;
  color: rgb(13, 17, 58);
  padding: 16px;
  font-size: 25px;
  font-weight: bold;
  border: none;
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: rgb(13, 17, 58);
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-content a {
  color: white;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {
  background-color: rgb(129, 126, 126);
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}

/* Change the background color of the dropdown button when the dropdown content is shown */
.dropdown:hover .dropbtn {
  background-color: #97979e;
}

