input[type=number] {
/*for absolutely positioning spinners*/
  position: relative; 
  margin: 10px;
  padding: 5px;
  padding-right: 25px;
  font-size: 25px;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
    opacity: 1;
}

input[type=number]::-webkit-outer-spin-button, 
input[type=number]::-webkit-inner-spin-button {
        -webkit-appearance: inner-spin-button !important;
        width: 25px;
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
      }

input:invalid {
  border: 2px solid red;
}

input.budget:invalid+span:after {
  content: '✖ Please input a number between 0 and 100';
  color: red;
  padding-left: 5px;
}

input.income:invalid+span:after {
    content: '✖ Please input a positive number';
    color: red;
    padding-left: 5px;
  }

input:valid+span:after {
  content: '✓';
  padding-left: 5px;
  color: green;
}

